*
{
  margin: 0;
  padding: 0;
  transition: 0.25s ease all;
  font-family: 'Lato', sans-serif;
  --accent-color: #3B4CCC;
  --hover-color: #4757d4;
  --secondary-color: 49, 48, 54; /* #059905 */
  --primary-color: 214, 202, 193; /* #D6CAC1 */
  --modal-color: #dadada;
  --text-color: #020202;
  color: var(--text-color);
  -webkit-tap-highlight-color: rgba(0,0,0,0); 
}

body{
  background: var(--primary-color);
  background: url('../public/images/backgroundHumboldt.webp');
  background-size: cover;
  background-position: center;
}

@media screen and (min-width: 800px) {
  body
  {
    background-size: cover;
    background-attachment: fixed;
  }
}

@media (prefers-color-scheme: dark) { 
  *
  {
    --accent-color: #4A5FFF;
    --hover-color: #4154E0;
    --primary-color: 49, 48, 54; /* #059905 */
    --secondary-color: 214, 202, 193; /* #D6CAC1 */
    --modal-color: #202020;
    --text-color: #ededed;
  }
}