.loader {
  width: 100%;
  height: 0.5rem;
  left: 50%;
  transform: translate(-50%, -50%);
  display: inline-block;
  position: relative;
  overflow: hidden;
}
.loader::after {
  content: '';  
  width: 96px;
  height: 4.8px;
  border-radius: 5%;
  background: var(--text-color);
  position: absolute;
  top: 0;
  left: 0;
  box-sizing: border-box;
  animation: hitZak 0.6s ease-in-out infinite alternate;
}

@keyframes hitZak {
  0% {
    left: 5%;
    transform: translateX(-5%);
  }
  100% {
    left: 95%;
    transform: translateX(-95%);
  }
}