.infoModalReloadBtn
{
  background-color: var(--accent-color);
  margin-top: 1.5rem;
  height: 3rem;
  width: 30%;
  min-width: fit-content;
  font-size: 1.5rem;
  border: none;
  border-radius: 1rem;
  padding: 0.5rem;
  cursor: pointer;
}


@media (hover: hover) {
  .infoModalReloadBtn:hover 
  {
    background-color: var(--hover-color);
    transform: scale(1.01);
  }
}