.modalBackdrop
{
  background-color: rgba(var(--primary-color), 0.5);
  backdrop-filter: blur(20px);
  position: fixed;
  inset: 0;
  z-index: 50;
  display: block;
}

.modalDiv
{
  display: flex;
  height: 100vh;
  justify-content: center;
  align-items: center;
  opacity: 1;
}

.modal
{
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  background-color: var(--modal-color);
  width: 90%;
  height: 10rem;
  padding: 0.75rem;
  box-sizing: border-box;
  display: flex;
  flex-direction: column;
  align-items: center;
  border-radius: 1rem;
  box-shadow: 0 0px 1rem rgba(var(--primary-color), 0.9);
}

.modal > p
{
  font-size: 1.25rem;
  text-align: center;
  margin-top: 1rem;
}

.loginBtn
{
  margin-top: 1.5rem;
}

@media screen and (min-width: 800px) {
  .modal
  {
    width: 50%;
  }
}

@media (hover: hover) {
  .loginBtn:hover 
  {
    transform: scale(1.1);
  }
}